<template>
  <div class="rank" ref="rank">
    <scroll class="toplist" ref="toplist" :data="ranklist">
      <ul >
        <li class="item" v-for="item in ranklist" @click="setitem(item)">
          <div class="icon">
            <img width="100" height="100" v-lazy="item.picUrl"/>
          </div>
          <ul class="songlist">
            <li class="song" v-for="eltm in item.songList">
              <span>{{eltm.songname}}——</span>
              <span>{{eltm.singername}}</span>
            </li>
          </ul>
        </li>
      </ul>
      <div class="loading-container" v-show="!ranklist.length">
        <loading></loading>
      </div>
    </scroll>
    <router-view></router-view>
  </div>
</template>

<script type="text/ecmascript-6">
  import Scroll from 'base/scroll/scroll'
  import Loading from 'base/loading/loading'
  import {ERR_OK} from 'api/config'
	import {getRank} from 'api/rank'
  import {playlistMixin} from 'common/js/mixin'
  import {mapMutations} from 'vuex'

  export default {
    mixins: [playlistMixin],
    data(){
      return {
        ranklist:[]
      }
    },
		created(){
			this._getRank()
		},
		methods:{
      setitem(item){
        this.setRankList(item)
        this.$router.push({
          path:`/rank/${item.id}`
        })
      },
      handlePlaylist(playlist){
        const bottom = playlist.length ? '60px': ''
        this.$refs.rank.style.bottom = bottom
        this.$refs.toplist.refresh()
      },
				_getRank(){
				getRank().then((res)=>{
          if(res.code==ERR_OK){
					this.ranklist = res.data.topList
          console.log(this.ranklist)
          }
				})
			},
      ...mapMutations({
        setRankList:'SET_RANKLIST'
      })
		},
		components:{
			Scroll,
			Loading
		}
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .rank
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .toplist
      height: 100%
      overflow: hidden
      .item
        display: flex
        margin: 0 20px
        padding-top: 20px
        height: 100px
        &:last-child
          padding-bottom: 20px
        .icon
          flex: 0 0 100px
          width: 100px
          height: 100px
        .songlist
          flex: 1
          display: flex
          flex-direction: column
          justify-content: center
          padding: 0 20px
          height: 100px
          overflow: hidden
          background: $color-highlight-background
          color: $color-text-d
          font-size: $font-size-small
          .song
            no-wrap()
            line-height: 26px
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>


